.first-layer {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #fff;
  z-index: 3;
}
.second-layer {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #151515;
  z-index: 3;
}

.loader-box {
  @media (max-width: 760px) {
    display: none;
  }
  width: 100%;
  height: 100vh;
  position: absolute;
  z-index: 99;
  top: 0;
  left: 0;

  .temp {
    position: absolute;
    width: 100%;
    height: 100vh;
    background: url("../../assets/images/gif1.mp4") no-repeat 50% 50%;
    background-size: cover;
    animation: fadeOut 3.8s linear forwards;
    opacity: 1;

    video {
      position: fixed;
      right: 0;
      bottom: 0;
      min-width: 100%;
      min-height: 100%;
    }
  }

  @keyframes fadeOut {
    0% {
      opacity: 0;
    }
    40% {
      opacity: 0;
    }
    41% {
      opacity: 1;
    }
    91% {
      opacity: 1;
    }
    92% {
      opacity: 0;
    }
    100% {
      opacity: 0;
    }
  }

  .first-block {
    position: absolute;
    width: 0%;
    height: 100vh;
    background: #151515;
    animation: go-left 3s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
    left: 0;
    animation-delay: 2;
  }

  @keyframes go-left {
    0% {
      left: 0;
      width: 0%;
    }
    50% {
      left: 0;
      width: 100%;
    }
    100% {
      left: 100%;
      width: 0;
    }
  }

  .second-block {
    position: absolute;
    width: 0%;
    height: 100vh;
    background: #151515;
    animation: go-left 3s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
    animation-delay: 2s;
  }

  .loader {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .loader ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  .loader ul li {
    display: inline-block;
    color: #fff;

    font-weight: bolder;
    margin: 0 8px;
    font-size: 24px;
    text-transform: uppercase;
    animation: loadNow 3s ease-in-out forwards;
    opacity: 0;
    transform: translateX(0px);
  }

  @keyframes loadNow {
    0% {
      opacity: 0;
      transform: translateX(80px);
    }
    20% {
      opacity: 1;
      transform: translateX(0px);
    }
    80% {
      opacity: 1;
      transform: translateX(0px);
    }
    100% {
      opacity: 0;
      transform: translateX(-80px);
    }
  }

  .loader ul li:nth-child(1) {
    animation-delay: 1.1s;
  }

  .loader ul li:nth-child(2) {
    animation-delay: 1.2s;
  }

  .loader ul li:nth-child(3) {
    animation-delay: 1.3s;
  }

  .loader ul li:nth-child(4) {
    animation-delay: 1.4s;
  }

  .loader ul li:nth-child(5) {
    animation-delay: 1.5s;
  }

  .loader ul li:nth-child(6) {
    animation-delay: 1.6s;
  }

  .loader ul li:nth-child(7) {
    animation-delay: 1.6s;
  }
}
